.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: #1f2937; /* Dark background */
  padding: 60px;
  border-radius: 8px;
  width: 50%;
  height: 60%;
  max-width: 90%;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: #fff;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 24px;
  color: #fff;
  cursor: pointer;
}

.modal-input,
.modal-textarea {
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  border-radius: 4px;
  border: none;
  outline: none;
}

.modal-input {
  height: 40px;
}

.modal-textarea {
  height: 100px;
}
#newTask{
  background: linear-gradient(
    to right,
    rgb(18, 8, 168),
    rgb(247,12,204),
    rgb(113, 40, 116),
    rgb(79, 77, 156),
    rgb(250,78,10)
    );
    background-clip: text;
    color: transparent;
}
.submit-button {
  width: 100%;
  padding: 10px;
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  
}
