.task {
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  box-shadow: 0px 9px 9px rgba(14, 4, 4, 0.42);
  
}

.task-content {
  flex-grow: 1; /* Allows the content to take up all the space above the buttons */
  margin-bottom: 10px; /* Adds some space between the content and buttons */
}

.task-buttons {
  padding-left: 2px;
  margin-left: -7%;
  display: flex;
  justify-content: space-between; /* Distributes buttons evenly across the bottom */
  gap: 4px; /* Adds space between the buttons */
  
}

.task-buttons button {
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  flex: 1; /* Ensures buttons take equal space */
  text-align: center;
  background-color: rgb(159, 155, 165);  
}

.task-buttons button.completed {
color: green;
  
}

.task-buttons button.incompleted {
  color: red;

}

#icon{
  background: linear-gradient(
    to right,
    rgb(250,78,10),
    rgb(247,12,204),
    rgb(0,106,255),
    rgb(79, 77, 156),
    rgb(250,78,10)
    );
    background-clip: text;
    color: transparent;
}
