  .sidebar {
    margin-top: 1.5%;
    position: sticky;
    top: 0;
    height: calc(100vh - 90px);
    overflow-y: auto;
    border-radius: 20px;
    width: 200px;
    padding: 20px;
    box-shadow: 0px 9px 9px rgba(14, 4, 4, 0.42);
    background-color: rgba(240, 239, 239, 0.822);
    transition: width 0.3s ease;
    

  }
  
  i {
    background: linear-gradient(
      to right,
      rgb(250, 78, 10),
      rgb(247, 12, 204),
      rgb(0, 106, 255),
      rgb(79, 77, 156),
      rgb(250, 78, 10)
    );
    background-clip: text;
    color: transparent;
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
    margin-top: 20px;
  }
  
  .sidebar li {
    margin: 10px 0;
    padding: 8px 0;
  }
  
  .sidebar li:hover {
    background-color: rgba(159, 155, 165, 0.4);
    border-radius: 10px;
  }
  
  .sidebar a {
    color: black;
    text-decoration: none;
    font-weight: bold;
    display: flex;
    align-items: center;
  }

  
  @media (min-width: 992px) {
    #icon{
      margin-top: -5%;
      margin-right: 3px;
      font-size: 20px;
    }
    .new{
      display: none; /* Hides the element */
      visibility: hidden;    }
   
    .sidebar ul {
      margin-top: 60%;
    }
  
    .sidebar li {
      margin: 30px 0;
      padding: 6px 0;
    }
  }
  @media (max-width: 991px) {
    #icon{
      margin-right: 3px;
      margin-top: -5%;
      font-size: 20px;
    }
    .new{
      display: none; /* Hides the element */
      visibility: hidden;    }
      .sidebar ul {
        margin-top: 60%;
      }
    
      .sidebar li {
        margin: 30px 0;
        padding: 6px 0;
      }
  }
  @media (max-width: 768px) {
    .new{
      display: block; 
      visibility: visible;
      margin-bottom: 10px;
     
    }
    .sidebar {
      display: none;
    }
    i{
      font-size: 25px;
      }
      
  }
  
  @media (max-width: 576px) {
    .new{
      display: block; 
      visibility: visible;
      margin-bottom: 10px;

    }
    i{
    font-size: 25px;
    }
   
    .sidebar {
      display: none;
   
    }
  
  }
  